            let price = 65;
            await this.client.placeOrderForEvent(eventId,side, orderType,qty,price);
        },
        getEventId: async function(description, endTime,createdBy){
            const eventId = await this.client.getEventId(description, endTime,createdBy);
            console.log({eventId});
            return eventId;
        },
        getOrdersForEvent: async function(){
            const eventId = await this.getEventId('Will Nifty cross 20k next month', 1656602070, '0x02D99535E1BEFb3B31c37def6A87767773763aCF');
        
            const orders = await this.client.getOrdersForEvent(eventId);
            console.log(JSON.stringify(orders));
        },
