            await this.client.approve(10000);
        },
        phoneShare: async function(){
            const url = window.location.href + '/0x02D99535E1BEFb3B31c37def6A87767773763aCF';
            const text = "You've been invited to play an Insta Game  ðŸŽ‰";
            navigator
            .share({
                text,
